<template>
    <div>
        <section>
            <h1>Add new section</h1>
            <hr><br>

            <form @submit.prevent="submit">
                <div class="mb-3">
                    <label for="title" class="form-label">Title:</label>
                    <input type="text" name="title" v-model="form.title" class="form-control" />
                </div>
                <button type="submit" class="btn btn-primary">Submit</button>
            </form>
        </section>

        <br><br>

        <section>
            <h1>Board</h1>
            <hr><br>

            <div v-if="sections" class="d-flex flex-row" style="gap: 5px">
                <div v-for="section in sections" :key="section.id">
                    <div class="card">
                        <div class="card-header bg-primary text-white text-capitalize">{{ section.title }}
                            <button @click.prevent="delSection(section.id)" class="btn btn-danger btn-sm">â¨‰</button>
                        </div>
                        <ul class="card-body">
                            <div v-if="section.note.length > 0">
                                <div v-for="note in section.note" :key="note.id" class="card">
                                    <ul class="list-group">
                                        <li class="list-group-item"><strong>Title: </strong>{{ note.title }}</li>
                                        <li class="list-group-item"><strong>Description: </strong>{{ note.description }}</li>
                                        <li class="list-group-item"><strong>Comments: </strong>{{ note.comments }}</li>
                                        <li class="list-group-item"><strong>Deadline: </strong>{{ note.deadline }}</li>
                                    </ul>
                                </div>
                            </div>
                            <div v-else>
                                <p>There is no notes.</p>
                            </div>
                        </ul>
                    </div>
                </div>
            </div>
            <div v-else>
                <p>Nothing to see here.</p>
            </div>
        </section>
    </div>
</template>

<script>
import { defineComponent } from 'vue';
import { mapGetters, mapActions } from 'vuex';

export default defineComponent({
    name: "Board",
    data() {
        return {
            form: {
                title: "",
            },
            // my_key: 0
        }
    },
    created: function() {
        return this.$store.dispatch("getSections")
    },
    computed: {
        ...mapGetters({sections: "stateSections", notes: "stateNotes"})
    },
    methods: {
        ...mapActions(["createSection"]),
        ...mapActions(["deleteSection"]),
        async submit() {
            await this.createSection(this.form)
        },
        async delSection(id) {
            await this.deleteSection(id)
            return this.$store.dispatch("getSections")
            // this.forceUpdate()
        },
        // forceUpdate() {
        //     this.my_key += 1
        // }
    }
})
</script>